import accountapi from '../../api/account'
const state = {
    bonus: {
      ones_amount: "",
      dragon_amount: "",
      per_ones_income: "",
      user_income: "",//DT
      rewarded: "",
      bonus_user_reward: ""
    },
    usdt_bonus: {
      ones_amount: "",
      dragon_amount: "",
      per_ones_income: "",
      user_income: "",//
      rewarded: "",//
      bonus_user_reward: "" //Usdt
    },
    ones: {
      all: '', //
      released: '',
      reward: '',
      freezed: '', //
      retire: '', //
      stage: '', //
      factor: '',//
      ones_balance:'',//
    },
    user: {
      all: '', // 2
      reward: '',//
      released: '',//
      unreleased: '',//
    },
    stake:{
      all_amount: '', //
      unstake_remain_time: '',
      user_amount: "",
      unstake_amount: '',
      stake_amount: '',
    },
    redeemNum:'',//
    pledgeNum:'',//
}

const getters = {
  bonus: state => {
    return state.bonus;
  },
  usdt_bonus: state => {
    return state.usdt_bonus;
  },
  ones: state => {
    return state.ones;
  },
  user: state => {
    return state.user;
  },
  stake: state => {
    return state.stake;
  }
}

const mutations = {
  ONES_BONUS(state,data){
    state.bonus = data.bonus;
    state.usdt_bonus = data.usdt_bonus;
    state.ones = data.ones;
    state.user = data.user;
    state.stake = data.stake;
  }
}

const actions = {
  // ones
  getOnesBonus({commit}) {
    accountapi.getOnesBonus().then(res=>{
      if (res.code == '0') {
        commit('ONES_BONUS',res.data)
      }
    })
  },
  // goPledgeOnes({commit},content){
  //   console.log('-----------2 -----',content.num)
  // },
  // goRedeemOnes(){
  //   console.log('-----------2')
  // },
}

export default {
  // namespaced: true,
  state,
  getters,
  mutations,
  actions,
}


