<template>
  <div class="daoContainer" ref="daoContainer">
    <div class="daoContent">
      <div class="box-card" v-if="id==1">
        <div class="dao-title"></div>
        <p
          class="dao-desc"
        >ONESONESONESONES251080%ONESONES</p>
        <div class="dao-title"></div>
        <div
          class="dao-desc"
        >1(): 50%ONESONES100ONES2EOS1EOSONES</div>
        <div
          class="dao-desc"
        >2(): 3020%ONES 9010%ONES 1805%ONES 180ONES</div>
        <div class="dao-box">
          <div class="dao-title"></div>
          <div class="dao-desc">1ONESONES30%</div>
          <div class="dao-desc">2</div>
          <div class="dao-desc">3</div>
        </div>
      </div>

      <div class="box-card" v-if="id==2">
        <div class="dao-title"></div>
        <p
          class="dao-desc"
        >0.020.011/2</p>
        <div class="dao-title"></div>

        <p class="dao-desc">10.01</p>
        <p class="dao-desc">2) 0.020.01*/**/</p>
        <div class="dao-box">
          <div class="dao-title"></div>
          <div class="dao-desc">1ONESONES30%</div>
          <div class="dao-desc">2</div>
          <div class="dao-desc">3</div>
        </div>
      </div>

      <el-card class="dao-card">
        <div slot="header" class="dao-card-header">
          <span></span>
          <span v-if="id==2">:{{my_remain_ticket}}</span>
        </div>
        <div>
          <div class="optionsCont" v-if="id==1">
            <div class="list">
              <div class="backProgress" :style="'width:'+this.percents[0]+'%;'"></div>
              <div class="listWord">
                <div style="font-weight: bold;">()</div>
                <div>: {{this.scores[0]}}{{this.percents[0]}}%</div>
              </div>
              <div class="icon"></div>
            </div>
            <div class="list">
              <div class="backProgress" :style="'width:'+this.percents[1]+'%;'"></div>
              <div class="listWord checkedCls">
                <div style="font-weight: bold;">()</div>
                <div>: {{this.scores[1]}}{{this.percents[1]}}%</div>
              </div>
              <div class="icon"></div>
            </div>
          </div>
           <div class="optionsCont" v-if="id==2">
            <div class="list">
              <div class="backProgress" :style="'width:'+this.percents[0]+'%;'"></div>
              <div class="listWord">
                <div style="font-weight: bold;"></div>
                <div>: {{this.scores[0]}}{{this.percents[0]}}%</div>
              </div>
              <div class="icon"></div>
            </div>
            <div class="list">
              <div class="backProgress" :style="'width:'+this.percents[1]+'%;'"></div>
              <div class="listWord checkedCls">
                <div style="font-weight: bold;"></div>
                <div>: {{this.scores[1]}}{{this.percents[1]}}%</div>
              </div>
              <div class="icon"></div>
            </div>
          </div>
          <div style="text-align:center;" v-if="id==1">2,</div>
        </div>
        <div class="dao-action" v-if="id==2">
          <div class="dao-action-option">
            <div class="dao-action-item"></div>
            <div class="dao-action-item">
              <el-input-number
                v-model="option_num1"
                size="small"
                @change="handleChange"
                :min="0"
                label=""
              ></el-input-number>
              <div class="dao-action-myticket">{{this.myscores[0]}}</div>
            </div>
          </div>
          <div class="dao-action-option" v-if="id==2">
            <div class="dao-action-item"></div>
            <div class="dao-action-item">
              <el-input-number
                v-model="option_num2"
                size="small"
                @change="handleChange"
                :min="0"
                label=""
              ></el-input-number>
              <div class="dao-action-myticket">{{this.myscores[1]}}</div>
            </div>
          </div>

          <el-button v-if="id==2" type="primary" :loading="isVoteLoading" @click="vote"></el-button>
        </div>
      </el-card>

      <el-card class="dao-card dao-log-card">
        <div slot="header" class="dao-card-header">
          <span> </span>
        </div>
        <div class="dao-log-card-body">
          <div>
            <span></span>
            <span>{{user}}</span>
          </div>
          <div>
            <span></span>
            <span>{{allscore}}</span>
          </div>
          <div>
            <span></span>
            <span>{{percent}}</span>
          </div>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
import Storage from '../../utils/storage'
import i18n from '../../locales/index'
import Util from '../../utils/util'
import EosService from '../../utils/eosservice'

export default {
  data() {
    return {
      category: 'market',
      mineContract: 'onesgamemine',
      defiContract: 'onesgamedefi',
      voteContract: 'onesgamevote',

      ruleDialogVisible: false,

      option_num1: 0,
      option_num2: 0,

      id: 0,
      scores: [0, 0],
      percent: 0,
      percents: [0, 0],
      myscores: [0, 0],
      allscore: 0,
      user: 0,
      my_all_ticket: 0,
      my_remain_ticket: 0,

      my_liquidity_ticket: 0,
      my_node_ticket: 0,
      my_lock_ticket: 0,

      isVoteLoading: false
    }
  },
  computed: {
    eosio: function() {
      return this.$store.state.eosio
    },

    account: function() {
      return this.$store.state.eos_account
    }
  },
  beforeMount() {
    this.id = this.$route.params.id
  },
  mounted() {
    this.get()
  },
  watch: {},
  beforeDestroy() {},
  methods: {
    vote() {
      this.isVoteLoading = true

      var actions = [
        {
          account: this.voteContract,
          name: 'vote',
          authorization: [
            {
              actor: this.account.name,
              permission: this.account.authority
            }
          ],
          data: {
            account: this.account.name,
            proposal_id: this.id,
            scores: [this.option_num1, this.option_num2]
          }
        }
      ]
      this.swapLoading = true
      this.eosio.transaction(
        actions,
        function(code, msg, data) {
          this.isVoteLoading = false
          if (code == 0) {
            this.$message({
              message: '',
              type: 'success'
            })

            setTimeout(
              function() {
                this.get()
              }.bind(this),
              2000
            )
          } else {
            this.swapLoading = false
            this.$message({
              message: msg,
              type: 'error'
            })
          }
        }.bind(this)
      )
    },
    get() {
      this.getProposal()
      this.getAccount()
    },
    getProposal() {
      this.eosio.getTableRows(
        {
          code: this.voteContract,
          scope: this.voteContract,
          table: 'proposal',
          limit: 1,
          lower_bound: this.id,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          if (data && data.rows && data.rows.length == 1) {
            var row = data.rows[0]
            if (row.proposal_id == this.id) {
              this.scores = row.scores
              this.allscore = 0
              this.percent = '0.00%'
              for (var i in row.scores) {
                this.allscore += row.scores[i]
              }
              console.log('allscore', this.allscore, ':........')
              for (var i in row.scores) {
                this.percents[i] =
                  this.allscore == 0
                    ? '0.00'
                    : Number((row.scores[i] * 100) / this.allscore).toFixed(2)
              }

              this.percent =
                Number((this.allscore / row.cur_supply) * 100).toFixed(2) + '%'
              this.user = row.user
            }
          }
        }.bind(this)
      )
    },
    getAccount() {
      this.eosio.getTableRows(
        {
          code: this.voteContract,
          scope: this.voteContract,
          table: 'account',
          limit: 1,
          lower_bound: this.account.name,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          if (data && data.rows && data.rows.length == 1) {
            var row = data.rows[0]
            if (row.account == this.account.name) {
              this.my_all_ticket = row.ticket
              this.my_liquidity_ticket = row.liquidity_ticket
              this.my_node_ticket = row.node_ticket
              this.my_lock_ticket = row.lock_ticket
            }
          }
          this.getTickets()
        }.bind(this)
      )
    },
    getTickets() {
      this.eosio.getTableRows(
        {
          code: this.voteContract,
          scope: this.id,
          table: 'tickets',
          limit: 1,
          lower_bound: this.account.name,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          this.my_remain_ticket = this.my_all_ticket
          if (data && data.rows && data.rows.length == 1) {
            var row = data.rows[0]
            console.log(row.account, this.account.name, 'xxxxxxx')
            if (row.account == this.account.name) {
              console.log(row.account, this.account.name, 'xxxxxxx')

              this.my_remain_ticket = this.my_all_ticket - row.ticket

              this.myscores = row.scores
            }
          }
        }.bind(this)
      )
    }
  },
  filters: {
    numberFilter(value) {
      return Number(value / 10000).toFixed(4)
    },

    numbersFilter(value) {
      var arr = []
      var tokens = ['ONES']

      for (var i in value) {
        if (tokens[i]) {
          if (tokens[i] == 'BOX') {
            arr.push(Number(value[i] / 1000000).toFixed(6) + ' ' + tokens[i])
          } else {
            arr.push(Number(value[i] / 10000).toFixed(4) + ' ' + tokens[i])
          }
        }
      }
      return arr.join(' | ')
    },
    weightFilter(value) {
      return Number(value).toFixed(1)
    }
  }
}
</script>

<style scoped>
.daoContainer {
  background-color: #060721;
  position: relative;
}
.daoContent {
  padding: 15px;
}
.daoContent /deep/ .el-card {
  background-color: #060721;
  border: 1px solid rgb(140, 174, 197) !important;
}
.box-card {
  background-color: #060721;
  border: 1px solid rgb(88, 111, 153);
  margin-bottom: 15px;
}
.box-card /deep/ .el-card {
  border: 1px solid rgb(140, 174, 197) !important;
  border-radius: 10px;
}
.banner {
  box-sizing: border-box;
  padding-top: 0.8rem;
  margin-bottom: 0.8rem;
  height: 4.4rem;
  background: #040718;
  border-radius: 10px;
}
.banner p {
  text-align: center;
}
.banner p:first-child {
  font-size: 18px;
  color: #f69b4a;
  font-weight: 500;
}

.daoContent /deep/ .el-card__body {
  padding: 10px 20px;
}

.daoContent /deep/ .el-card {
  color: rgb(241, 241, 241);
}

.daoContent .box-card {
  /* border: 1px solid #5e77af;
  border-radius: 10px; */
  background: #040718;
  border: 1px solid rgb(88, 111, 153);
  border-radius: 10px;
  color: #fafbfd;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  -webkit-box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  padding: 10px 10px 5px;
  position: relative;
}

.daoContent .box-card .card-body {
  padding-left: 10px;
}

.mine-card-header {
  display: flex;
  justify-content: space-between;
}

.card-item {
  display: flex;
  justify-content: space-between;
}

.dao-item {
  display: flex;
  justify-content: space-between;
}

.optionsCont .list {
  color: #333;
  position: relative;
  padding: 0.4rem 0.62667rem;
  background: #fff;
  margin-bottom: 0.29333rem;
  border-radius: 0.10667rem;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
}

.optionsCont .list .backProgress {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 0;
  height: 100%;
  background: rgba(254, 183, 94, 0.2);
  border-radius: 0.10667rem 0 0 0.10667rem;
  z-index: 1;
}

.optionsCont .list .listWord {
  position: relative;
  -webkit-align-self: start;
  align-self: start;
  font-size: 0.41333rem;
  margin-top: -0.06667rem;
  z-index: 2;
}

.optionsCont .list .listWord div:nth-child(2) {
  font-size: 0.34667rem;
  margin-top: 0.08rem;
}

.optionsCont .list .icon {
  -webkit-align-self: start;
  align-self: start;
  margin-top: -0.02667rem;
}

.dao-action button {
  width: 100%;
  margin-top: 10px;
}

.dao-action .dao-action-option {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

.dao-action .dao-action-option .dao-action-item {
  line-height: 40px;
}

.dao-title {
  font-size: 18px;
  font-weight: 500;
  padding-bottom: 5px;
  margin-bottom: 5px;
  margin-top: 10px;
  border-bottom: #ccc solid 1px;
}
.dao-desc {
  font-size: 13px;
}

.dao-card .dao-card-header {
  display: flex;
  justify-content: space-between;
}

.dao-log-card {
  margin-top: 10px;
}
.dao-log-card-body div {
  display: flex;
  justify-content: space-between;
}
.dao-action-myticket {
  text-align: center;
  line-height: 20px;
}
</style>


