  computed: {
    eosio: function() {
      return this.$store.state.eosio
    },
    account: function() {
      return this.$store.state.eos_account
    }
  },
  beforeMount() {
    if (!this.eosio) {
      var eosio = new EosService(process.env.EOS_CONTRACT_NAME)
      this.$store.dispatch('ASET_EOSIO', { eosio: eosio }).then(res => {
        this.connect()
      })
    }

    var key = Storage.get('ones_notify')
    if (key != '136') {
      Storage.set('ones_notify', '136')
      this.noticeDialogVisible = true
    }
  },
  beforeRouteUpdate(to, from, next) {
    if (to.name == 'defiMine') {
      this.isShowDoubleMine = false
    } else if (to.name == 'defiSwap' || to.name == 'defiMarket') {
      this.isShowDoubleMine = false //Storage.get('ones_double_mine') ? false : true
    } else {
      this.isShowDoubleMine = false
    }
    next()
  },
  mounted() {
    this.navStyleObj.width = this.$refs.defiContainer.clientWidth + 'px'

    this.ContainerStyleObj.height =
      this.$refs.defiContainer.offsetParent.clientHeight - 50 + 'px'

    if (this.$route.name == 'defiSwap' || this.$route.name == 'defiMarket') {
      this.isShowDoubleMine = false //Storage.get('ones_double_mine') ? false : true
    } else if (this.$route.name == 'defiMine') {
      this.isShowDoubleMine = false //todo
    }
  },
  watch: {
    walletDialogVisible(val1, val2) {
      if (val1) {
        this.getBalance()
      }
    }
  },
  methods: {
    openTip() {
      this.showRetire()
      this.tipVisable = false
    },
    closeTip() {
      this.tipVisable = false
    },
    checkReport() {
      this.$router.push({
        name: 'defiContract'
      })
    },
    viewTrx(item) {
      window.location.href = 'https://bloks.io/transaction/' + item.trxid
      return
    },
    changeEosNode() {
      console.log(this.nodeRadio)
      var node = this.nodes[this.nodeRadio]
      Storage.set('ones_eosnode', node.url)
      window.location.reload()
    },
    log() {
      var uuid = Storage.get('ones_uuid')

      if (!uuid) {
        uuid = ''
        var len = len || 32
        var $chars =
          'ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678' /****oOLl,9gq,Vv,Uu,I1****/
        var maxPos = $chars.length
        for (var i = 0; i < len; i++) {
          uuid += $chars.charAt(Math.floor(Math.random() * maxPos))
        }
        Storage.set('ones_uuid', uuid)
      }

      EosApi.log({
        account: this.account.name,
        ref: this.$store.getters.ref,
        channel: this.$store.getters.channel,
        source: this.$store.getters.source,
        scene: 'login',
        uuid: uuid
      })
    },
    claim() {
      this.claimLoading = true
      var actions = [
        {
          account: 'onesgameplay',
          name: 'claim',
          authorization: [
            {
              actor: this.account.name,
              permission: this.account.authority
            }
          ],
          data: {
            account: this.account.name
          }
        }
      ]
      console.log(actions)
      this.eosio.transaction(
        actions,
        function(code, msg, data) {
          console.log(code, msg, data)
          this.claimLoading = false

          if (code == 0) {
            this.$message({
              message: '',
              type: 'success',
              center: true
            })
            setTimeout(
              function() {
                this.getRelease()
              }.bind(this),
              1000
            )
            setTimeout(
              function() {
                this.getRelease()
              }.bind(this),
              2000
            )
          } else {
            this.$message({
              message: msg,
              type: 'error'
            })
          }
        }.bind(this)
      )
    },
    showRelease() {
      this.releaseDialogVisible = true
      this.getRelease()
    },
    getRelease() {
      this.eosio.getTableRows(
        {
          code: 'onesgameplay',
          scope: 'onesgameplay',
          table: 'lockuser',
          limit: 1,
          lower_bound: this.account.name,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          if (
            code == 0 &&
            data.rows &&
            data.rows.length == 1 &&
            data.rows[0]['account'] == this.account.name
          ) {
            var row = data.rows[0]
            this.lockuser = row
          }
        }.bind(this)
      )
    },
    showStake() {
      this.stakeDialogVisible = true
      this.stakeTableDialogVisible = false
      this.stakeLoading = false
      this.getStake()
    },
    getStake() {
      this.eosio.getTableRows(
        {
          code: 'onesgameplay',
          scope: 'onesgameplay',
          table: 'stakeinfo',
          limit: 1,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          if (code == 0 && data.rows && data.rows.length == 1) {
            var row = data.rows[0]
            this.stakeInfo.total = row.ones2
            this.stakeInfo.percent = Number(
              (parseFloat(row.ones2) / 150000) * 100
            ).toFixed(2)
          }
          this.getMyStake()
        }.bind(this)
      )
    },

    getMyStake() {
      this.eosio.getTableRows(
        {
          code: 'onesgameplay',
          scope: 'onesgameplay',
          table: 'stakeuser2',
          limit: 1,
          key_type: 'i64',
          lower_bound: this.account.name,
          json: true
        },
        function(code, msg, data) {
          if (
            code == 0 &&
            data.rows &&
            data.rows.length == 1 &&
            data.rows[0].account == this.account.name
          ) {
            var row = data.rows[0]
            this.stakeInfo.my = row.ones
            console.log('..........,', row, this.stakeInfo)
          }
        }.bind(this)
      )
    },
    stake() {
      this.stakeLoading = true
      var actions = [
        {
          account: 'eosonestoken',
          name: 'transfer',
          authorization: [
            {
              actor: this.account.name,
              permission: this.account.authority
            }
          ],
          data: {
            from: this.account.name,
            to: 'onesgameplay',
            quantity: Number(this.stakeAmount).toFixed(4) + ' ONES',
            memo: 'stake'
          }
        }
      ]
      // console.log(actions)
      this.eosio.transaction(
        actions,
        function(code, msg, data) {
          console.log(code, msg, data)
          this.stakeLoading = false

          if (code == 0) {
            this.$message({
              message: '',
              type: 'success',
              center: true
            })
            setTimeout(
              function() {
                this.getStake()
              }.bind(this),
              1000
            )
            setTimeout(
              function() {
                this.getStake()
              }.bind(this),
              2000
            )
          } else {
            if (msg.indexOf('Not-User') >= 0) {
              msg = ''
            } else if (msg.indexOf('START') >= 0) {
              msg = ''
            } else if (msg.indexOf('END') >= 0) {
              msg = ''
            } else if (msg.indexOf('min-amount-invaild') >= 0) {
              msg = '100ONES'
            } else if (msg.indexOf('exceed') >= 0) {
              msg = '25'
            }

            this.$message({
              message: msg,
              type: 'error'
            })
          }
        }.bind(this)
      )
    },
    showRetire() {
      this.retireDialogVisible = true
      this.eosio.getOnesBalance(
        'onesgamehole',
        function(balance) {
          this.retireBalance = balance
        }.bind(this)
      )
    },

    toDeFi() {
      this.$router.push({
        name: 'defiSwap'
      })
    },
    connect(cb) {
      this.eosio.connect(
        function(code) {
          if (code == 1) {
            cb && cb(code)
            this.account = null
            return
          }
          console.log(this.eosio.account, 'xxxxxxxxxxxxxxxxxxxxxxxxxx')

          var account = this.eosio.account
          account.isonline = true
          this.$store.dispatch('ASET_EOS_ACCOUNT', account).then(res => {
            // this.account = account
            this.getBalance()
            this.log()
            // this.getMineAccount()
            cb && cb(code)
          })
        }.bind(this)
      )
    },
    // login() {
    //   if (this.eosio.account && this.eosio.account.name) {
    //     this.eosio.logout()
    //     this.account = ''
    //     return
    //   }
    //   var loading = this.$loading({
    //     lock: true,
    //     text: '',
    //     spinner: 'el-icon-loading',
    //     background: 'rgba(0, 0, 0, 0.7)'
    //   })
    //   this.eosio.connect(
    //     function(code, msg, data) {
    //       loading.close()
    //       if (code == 0) {
    //         Storage.set('ones_eos_account', this.eosio.account.name)
    //         this.account = this.eosio.account.name
    //         this._getMyMine()
    //         this.getBalances(0)
    //       } else {
    //         this.account = ''
    //         this.$message({
    //           message: ',Scatter',
    //           center: true,
    //           type: 'error'
    //         })
    //       }
    //     }.bind(this)
    //   )
    // },
    getBalance() {
      if (this.eosio && this.account) {
        this.eosio.getOnesBalance(
          this.account.name,
          function(balance) {
            this.onesBalance = balance
          }.bind(this)
        )
      }
    },

    showWallet() {
      this.active = 'wallet'
      this.walletDialogVisible = true
    },
    closeWallet() {
      this.walletDialogVisible = false
    },

    logout() {
      this.closeWallet()

      this.eosio.logout()
      this.account = null
      this.$store.dispatch('ASET_EOS_ACCOUNT', null)
      console.log('logout.....', this.account)
    },
    login() {
      const loading = this.$loading({
        lock: true,
        text: i18n.t('defiScene.logining'),
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      this.connect(
        function(code) {
          loading.close()
          this.closeWallet()
          if (code == 0) {
            this.$message({
              message: i18n.t('message.login_success'),
              type: 'success',
              center: true
            })
          } else {
            this.$message({
              message: i18n.t('message.login_failed'),
              type: 'error',
              center: true
            })
          }
        }.bind(this)
      )
    },

    oauth(status) {
      this.claimLoading = true

      if (status == 1) {
        this.oAuthLoading = true
      } else {
        this.cancelLoading = true
      }
      var actions = [
        {
          account: this.mineContract,
          name: 'oauth',
          authorization: [
            {
              actor: this.account.name,
              permission: this.account.authority
            }
          ],
          data: {
            account: this.account.name,
            status: status
          }
        }
      ]
      console.log(actions)
      this.eosio.transaction(
        actions,
        function(code, msg, data) {
          console.log(code, msg, data)
          this.claimLoading = false

          this.oAuthLoading = false
          this.cancelLoading = false

          if (code == 0) {
            this.$message({
              message: status == 0 ? '' : '',
              type: 'success',
              center: true
            })
          } else {
            this.$message({
              message: msg,
              type: 'error'
            })
          }
        }.bind(this)
      )
    },
    openDoubleMine() {
      Storage.set('ones_double_mine', 1)
      // this.getMineAccount()

      this.doubleMineDialogVisible = true
    },
    getMineAccount() {
      this.eosio.getTableRows(
        {
          code: this.mineContract,
          scope: this.mineContract,
          table: 'account',
          limit: 1,
          lower_bound: this.account.name,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          if (
            code == 0 &&
            data.rows &&
            data.rows.length == 1 &&
            data.rows[0]['account'] == this.account.name
          ) {
            var row = data.rows[0]
            this.isoAuth = row.oauth
          }
        }.bind(this)
      )
    },
    toMine() {
      this.$router.push({
        name: 'defiMine'
      })
    },
    toHelp() {
      this.$router.push({
        name: 'defiAbout'
      })
    },
    toBonus() {
      this.$router.push({
        name: 'nodeScene'
      })
    },
    toDao() {
      this.$router.push({
        name: 'defiDao'
      })
    },
    toPlay() {
      this.$router.push({
        name: 'gameScene'
      })
    },
    toBounty() {
      this.$router.push({
        name: 'defiBounty'
      })
    },
    toContract() {
      this.$router.push({
        name: 'defiContract'
      })
    },
    settingCommand(command) {
      console.log(command)
      if (command == 'mine') {
        this.toMine()
      } else if (command == 'help') {
        this.toHelp()
      } else if (command == 'bonus') {
        this.toBonus()
      } else if (command == 'dao') {
        this.toDao()
      } else if (command == 'bounty') {
        this.toBounty()
      } else if (command == 'play') {
        this.toPlay()
      } else if (command == 'notice') {
        this.noticeDialogVisible = true
      } else if (command == 'retire') {
        this.showRetire()
      } else if (command == 'release') {
        this.showRelease()
      } else if (command == 'stake') {
        this.showStake()
      } else if (command == 'contract') {
        this.toContract()
      } else if (command == 'contact') {
        this.contactDialogVisible = true
      } else if (command == 'eosnode') {
        this.eosNodeDialogVisible = true
      }
      return false
    }
  }
}
