<template>
  <div class>
    <div class="OutsideContainer" ref="OutsideContainer" :style="ContainerStyleObj">
      <div class="content">
        <router-view></router-view>
      </div>
    </div>
    <div class="nav-bar" ref="navbar" :style="navStyleObj" v-if="zone!='pandora'&&zone!='defi'">
      <ul>
        <li v-if="zone!='defi'">
          <router-link
            :to="{name:'gameScene'}"
            class="route-index"
            v-bind:class="{ active: active=='game' }"
          >
            <span>{{$t("gameScenePart.game")}}</span>
          </router-link>
        </li>
        <li v-if="zone=='eos'||zone=='defi'">
          <router-link
            :to="{name:'defiSwap'}"
            class="route-defi"
            v-bind:class="{ active: active=='defi' }"
          >
            <span>{{$t("gameScenePart.defi")}}</span>
          </router-link>
        </li>
        <li v-if="false&&zone!='defi'">
          <router-link
            :to="{name:'walletScene'}"
            class="route-wallet"
            v-bind:class="{ active: active=='wallet' }"
          >
            <span>{{$t("gameScenePart.wallet")}}</span>
          </router-link>
        </li>
        <li v-if="false&&zone!='defi'">
          <router-link
            :to="{name:'profileScene'}"
            class="route-my"
            v-bind:class="{ active: active=='profile' }"
          >
            <span>{{$t("gameScenePart.my")}}</span>
          </router-link>
        </li>
      </ul>
    </div>

    <el-dialog title="" :visible.sync="shareDialogVisible" :width="dialogWidth">
      <div id="programme">
        <h4 class="top-nav">3000ONES</h4>
        <div class="sub-nav">, ,</div>
        <ul>
          <li>12011ONES3010ones</li>
          <li>2ONES</li>
          <li>31001511ONES25ONES</li>
          <li>475ONES</li>
          <li>55%15%vip10,49999U15%</li>
          <li>6,</li>
        </ul>
        <div class="example">
          
          <p>
            300204-51120-3020-25300*1*10=
            <span
              style="color:#F56C6C;"
            >3000</span>
          </p>
          <p>
            30060=75 ONES*60=4500 ONESones/usdt=0.15u
            <span
              style="color:#F56C6C;"
            >4725</span>
          </p>
          <p>
            30030150u
            =150u*30*5%=225u
            <span
              style="color:#F56C6C;"
            >1575</span>
          </p>
          <br />
          <p style="color:#F56C6C;">
            
            3000+4725+1575=9300
          </p>
        </div>
      </div>
    </el-dialog>

    <el-dialog title="" :visible.sync="voteDialogVisible" :width="dialogWidth">
      <div id="programme">
        <h4 class="top-nav"></h4>
        <div class="sub-nav"></div>
        <ul>
          <li>
            1OnesPlay8
            ONES10ONES
          </li>
          <li>
            220%
            ONES
          </li>
          <li>1ONES3 04-27 20:00 ~ 04-28 20:00</li>
          <li>1ONES2.504-28 20:00 ~ 04-29 20:00</li>
          <li>1ONES204-29 20:00 ~ 04-30 20:00</li>
          <li>1ONES1.504-30 20:00 ~ 05-01 20:00</li>
          <li>1ONES105-01 20:00 ~ 05-02 20:00</li>

          <li>35ONES</li>
        </ul>
        <!-- <div class="example">
          
          <p>
            1010
            1300
            2204-51
            31
            420-3020-25
            5300*1*10=3000
          </p>
          <p>
            75ones3225ONES
            1300
            260=100 ONES*60=6000 ONESones/usdt=0.15u6300
          </p>
          <p>
            5%15%
            30030150u
            =150u*30*5%=75u1575
          </p>

          <p>
            
            3000+6300+1575=10875
          </p>
        </div>-->
      </div>
    </el-dialog>
  </div>
</template>

<script>
import asset from '../api/asset'
export default {
  data() {
    return {
      zone: '',
      theme: 'theme-default',
      dialogWidth: window.innerWidth > 720 ? '50%' : '90%',
      shareDialogVisible: false,
      subscribeDialogVisible: false,
      voteDialogVisible: false,
      active: 'game',

      navStyleObj: {
        width: ''
      },
      ContainerStyleObj: {
        height: '',
        background: '#f5f5f5'
      },
      islogin: window.localStorage.getItem('ones_account')
    }
  },

  methods: {
    isLogin() {
      return this.$store.state.account.isLogin
    }
  },

  watch: {
    $route: {
      handler: function(route) {
        var route_name = this.$route.name
        var path = this.$route.path
        if (path.indexOf('/asset') == 0 || route_name == 'walletScene') {
          this.active = 'wallet'
        } else if (route_name == 'gameScene' || path.indexOf('/game') == 0) {
          this.active = 'game'
        } else if (route_name == 'swapScene' || path.indexOf('/defi') == 0) {
          this.active = 'defi'
        } else {
          this.active = 'profile'
        }

        // if (route_name == 'walletScene') {
        //   // this.ContainerStyleObj.background = '#e7e5e5'
        // } else {
        this.ContainerStyleObj.background = '#f5f5f5'
        // }
      },
      immediate: true
    }
  },
  mounted() {
    this.zone = this.$store.getters.zone

    var agent = navigator.userAgent.toLowerCase()
    if (agent.indexOf('justalk') > 0) {
      this.theme = 'theme-white'
    }

    this.navStyleObj.width = this.$refs.OutsideContainer.clientWidth + 'px'

    // if (this.zone != 'defi') {
    //   this.ContainerStyleObj.height =
    //     this.$refs.OutsideContainer.offsetParent.clientHeight - 50 + 'px'
    // } else {
    this.ContainerStyleObj.height =
      this.$refs.OutsideContainer.offsetParent.clientHeight - 50 + 'px'
    // }
  }
}
</script>

<style scoped>
.OutsideContainer {
  width: 100%;
  overflow-y: scroll;
}
.OutsideContainer::-webkit-scrollbar {
  display: none;
}
.OutsideContainer .content {
  height: 100%;
}
.nav-bar {
  position: fixed;
  bottom: 0;
  /* left: 0;
  right: 0; */
  z-index: 10;
  height: 50px;
  /* background-color: rgb(65, 153, 247); */
  background-color: #fff;
  border-top: 2px solid #f5f5f5;
}
.nav-bar .title {
  white-space: nowrap;
  height: 50px;
  line-height: 50px;
  font-size: 1.5rem;
  color: #fff;
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
}
.router-link-right {
  height: 50px;
  float: right;
  line-height: 50px;
  font-size: 1.5rem;
  color: rgb(236, 236, 236);
  margin-right: 18px;
}
.router-link-left {
  height: 50px;
  float: left;
  line-height: 50px;
  font-size: 1.5rem;
  color: rgb(236, 236, 236);
  margin-left: 18px;
}
.nav-bar {
  /* background-color: rgb(65, 153, 247); */
  background-color: #fff;
}
.nav-bar ul {
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  -ms-flex-pack: distribute;
  justify-content: space-around;
  -ms-flex-align: center;
  align-items: center;
  height: 52px;
}

.nav-bar a {
  display: -ms-inline-flexbox;
  display: inline-flex;
  width: 100%;
  font-size: 10px;
  font-family: PingFangSC-Regular;
  color: #b2b2b2;
  text-align: center;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-pack: center;
  justify-content: center;
  padding-top: 24px;
  background-position: top;
  background-repeat: no-repeat;
  background-size: 20px 20px;
}

.nav-bar .active {
  color: rgb(65, 153, 247);
}

.nav-bar .route-index {
  background-image: url(../assets/game-icon.png);
}

.nav-bar .route-index.active {
  background-image: url(../assets/game-active-icon.png);
}

.nav-bar .route-wallet {
  background-image: url(../assets/wallet-icon.png);
}

.nav-bar .route-wallet.active {
  background-image: url(../assets/wallet-active-icon.png);
}

.nav-bar .route-my {
  background-image: url(../assets/my-icon.png);
}

.nav-bar .route-my.active {
  background-image: url(../assets/my-active-icon.png);
}

.nav-bar .route-defi {
  background-image: url(../assets/defi-icon.png);
}

.nav-bar .route-defi.active {
  background-image: url(../assets/defi-active-icon.png);
}
.nav-bar .title {
  color: #fff;
}

@media screen and (min-width: 760px) {
  .nav-bar {
    bottom: auto;
  }
}

.theme-default .router-link-right span a {
  font-size: 1.5rem;
  color: #fff;
}

.theme-default .router-link-left span a {
  font-size: 1.5rem;
  color: #fff;
}

.theme-white .nav-bar {
  background-color: rgb(255, 255, 255);
}
.theme-white .nav-bar .title {
  color: rgb(65, 153, 247);
}

.theme-white .router-link-right span a {
  font-size: 1.5rem;
  color: rgb(65, 153, 247);
}

.theme-white .router-link-left span a {
  font-size: 1.5rem;
  color: rgb(65, 153, 247);
}

#programme {
  width: 100%;
  text-align: left;
}
#programme .top-nav {
  font-size: 1.1rem;
  text-align: center;
  margin-bottom: 5px;
}
#programme .sub-nav {
  font-size: 1rem;
  /* line-height: 2rem; */
  text-align: left;
  line-height: 1.7rem;
}
#programme ul li {
  text-align: left;
  margin-bottom: 0.3rem;
  line-height: 1.3rem;
}
#programme .example {
  width: 97%;
  margin: 20px auto;
  box-sizing: border-box;
  background-color: rgb(202, 202, 202);
  padding: 10px 15px;
  border-radius: 20px;
  text-align: left;
  font-size: 0.7rem;
  line-height: 1.4rem;
}
/* /deep/ .el-dialog__body {
  padding: 10px 15px;
} */
</style>



